}
body.dark-mode #theme-toggle:hover {
  background-color: rgba(255, 255, 255, 0.1); /* 다크모드 호버 */
}


/* ========================================================================== */
/* === 메인 페이지 (MainPage.js) === */
/* ========================================================================== */
#app {
  padding-top: var(--navbar-height);
}
.main-content {
  display: flex;
  flex-direction: row;
  gap: 20px;
