  justify-content: space-between;
  align-items: center;
}
body.dark-mode .coin-item {
  /* background-color: #262936; 위에서 정의됨 */
}
.coin-item:hover {
  /* background-color: #f0f4ff; */
  /* border-color: #d0d9f0; */
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}
body.dark-mode .coin-item:hover {
  background-color: #2a2e3f; /* 위에서 정의됨 */
  border-color: #3a3e4f;
}
.coin-info-row {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 15px;
  /* margin-bottom: 8px; */
}
.coin-symbol-name {
  display: flex;
  align-items: baseline;
  font-weight: 500;
}
.coin-item-symbol {
  font-weight: bold;
  font-size: 1.1em;
  color: var(--text-color);
  margin-right: 4px;
}
.coin-item-name {
  font-size: 1.2em;
  color: var(--text-tertiary-color);
}
.coin-price {
  font-size: 1.2em;
  font-weight: 500;
  color: var(--text-color);
}
.coin-change {
  font-size: 1.0em;
  font-weight: 500;
  min-width: 60px;
  /* text-align: right; */
}
.daily-weather-row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
  /* margin-top: 10px; */
  /* padding-top: 10px; */
  /* border-top: 1px dashed var(--border-color); */
}
.weather-day-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 0.8em;
  min-width: 50px;
}
.weather-day-label {
  font-weight: 500;
  margin-bottom: 4px;
  color: var(--text-secondary-color);
  font-size: 0.9em;
}
.weather-day-icon {
  font-size: 1.8em;
}

.analysis-note {
  font-size: 0.75em;
  color: var(--text-tertiary-color);
  text-align: center;
  padding: 10px 20px;
  border-top: 1px solid var(--border-color);
  flex-shrink: 0;
  background-color: var(--card-bg-color);
}

.cta-buttons {
  display: flex;
  gap: 10px;
  padding: 15px 20px 20px 20px;
  background-color: var(--card-bg-color);
  border-top: 1px solid var(--border-color);
  flex-shrink: 0;
}
.cta-buttons button {
  flex-grow: 1;
  padding: 10px;
  border: none;
  border-radius: 8px;
  background-color: var(--primary-color);
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s;
}
.cta-buttons button:hover {
  background-color: var(--primary-hover-color);
}

.right-panel {
  flex: 2;
  display: flex;
  flex-direction: column;
  gap: 20px;
  position: -webkit-sticky;
  position: sticky;
  top: var(--main-sticky-panel-top);
  align-self: flex-start;
  height: var(--main-sticky-panel-height);
  overflow-y: auto;
  background: var(--background-color);
  border-radius: 12px;
  padding: 0;
}
.market-cap-panel,
.fear-greed-panel {
  position: static;
  /* background: var(--card-bg-color); */
  border-radius: 12px;
  /* padding: 20px; */
  box-shadow: var(--card-shadow);
  color: var(--text-color);
  /* border: 1px solid #fff; */
}
.market-cap-panel {
  min-height: 260px;
  display: flex;
  flex-direction: column;
}
.fear-greed-panel {
  min-height: 180px;
  display: flex;
  flex-direction: column;
}
.market-cap-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  flex-shrink: 0;
}
.market-cap-title {
  font-weight: bold;
  font-size: 1.3em;
  color: var(--text-secondary-color);
}
.market-cap-value-container {
  text-align: right;
  display: flex;
