}
.fear-greed-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  flex-shrink: 0;
}
.fear-greed-title {
  font-weight: bold;
  font-size: 1.3em;
  color: var(--text-secondary-color);
