}
.fear-greed-current-value-status {
  text-align: right;
  font-size: 0.8em;
  display: flex;
  align-items: baseline;
  gap: 10px;
}
.fear-greed-value-small {
  font-weight: bold;
  color: var(--text-color);
  font-size: 1.0em;
}
.fear-greed-status-small {
  color: var(--text-secondary-color);
  font-size: 0.8em;
}
.fear-greed-content {
  display: flex;
