  border: none;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  transition: color 0.2s, border-bottom-color 0.2s;
}

.tab-button:hover {
  color: var(--primary-color); /* 호버 시 색상 변경 */
}

.tab-button.active {
  color: var(--primary-color); /* 활성 탭 색상 */
