}

.discussion-top-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  gap: 16px;
  flex-wrap: wrap; /* 반응형 대응 */
}

.coin-filter label {
  font-size: 0.9em;
  color: var(--text-secondary-color);
  margin-right: 8px;
}

.coin-filter select {
  padding: 8px 12px;
  border: 1px solid var(--input-border-color);
