  font-size: 0.9em;
  background-color: var(--input-bg-color);
  color: var(--text-color);
  min-width: 150px;
}

.search-box {
  display: flex;
  flex-grow: 1;
  max-width: 400px;
}

.search-box input[type="text"] {
  padding: 8px 12px;
  border: 1px solid var(--input-border-color);
  border-radius: 6px 0 0 6px;
