}
.search-box button:hover {
  background-color: var(--primary-hover-color);
}
.search-box button svg {
  fill: white;
  vertical-align: middle;
}

.write-button { /* 네이버 그린 계열 또는 accent color 사용 */
  padding: 8px 16px;
  background-color: var(--accent-color);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 0.9em;
  font-weight: 500;
