  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
}

.write-form h3 {
  font-size: 1.4em;
  color: var(--text-color);
  margin-bottom: 20px;
  font-weight: 600;
  text-align: center;
}

.write-form label {
  font-size: 0.9em;
  font-weight: 500;
  color: var(--text-secondary-color);
  display: block;
  margin-bottom: 6px;
}

.write-form select,
.write-form input[type="text"],
.write-form textarea {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid var(--input-border-color);
