}

.write-form .form-buttons button {
  padding: 9px 18px;
  border: none;
  border-radius: 6px;
  font-size: 0.9em;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.write-form .form-buttons .cancel-button {
  background-color: var(--disabled-bg-color); /* 비활성 배경색 활용 */
