}
.post-detail-meta .post-author-avatar.detail-avatar { width: 32px; height: 32px; font-size: 0.9em; }
.post-detail-meta .post-author-name { font-weight: 500; color: var(--text-color); } /* 작성자 이름은 기본 텍스트 색상 */
